.CartItem {
    display: flex;
    margin: 0 50px;
    border: 1px solid #181f24;
    padding: 10px;
    gap: 40px;
    width: 90%;

}

.CartItem img {
    width: 100px;
    height: 100px;
    object-fit: cover;

}

.CartItem .CartItem_Description {
    display: flex;
    width: 100%;
    text-align: left;
    justify-content: space-between;
}

.CartItem .CartItem_Description .desc {
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.CartItem .CartItem_Description .desc .desc_title {
    font-size: 18px;
    width: 60%;
    font-weight: 500;
    letter-spacing: 2px;
    line-height: 1.5;
}

.CartItem .CartItem_Description .desc .desc_description {
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 1px;
    line-height: 1.5;
}

.CartItem .options {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 10px;
}

.CartItem .counter {
    display: flex;
}

.CartItem .counter .left_button {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;
    font-size: 24px;
    width: 38px;
    height: 38px;
    border: 1px solid #181f24;
    background-color: #FFFFFF;
    padding: 10px 16px;
    text-align: center;
}

.CartItem .counter .right_button {
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;
    width: 38px;
    height: 38px;
    font-size: 24px;
    border: 1px solid #181f24;
    background-color: #FFFFFF;
    padding: 10px 16px;
}

.CartItem .counter input {
    display: flex;
    justify-content: right;
    align-items: center;
    text-align: right;
    font-size: 12px;
    width: 38px;
    height: 38px;
    background-color: #FFFFFF;
    border: 1px solid #181f24;
}

.CartItem a {
    text-align: right;
    letter-spacing: 1px;
    font-weight: 300;
    text-decoration: none;
    cursor: pointer;
    color: #181f24;
}